<template>
  <div class="dental-exam-finding-editor">
    <div class="editor-section">
      <h3 class="section-title">Clasificacion</h3>
      <p class="finding-type-name" v-html="name" />
    </div>
  </div>
</template>

<script>
/* paper */
import Paper from "paper";

export default {
  name: "DentalExamFindingEditor",

  props: {
    value: {
      type: Object
    },
    createMode: Boolean,
    editMode: Boolean,
    activeMouseTool: String,
    activeMouseToolColor: String,
    isEditorVisible: Boolean
  },
  data() {
    return {
      selectFindingTypeModalVisible: false
    };
    // ,
  },
  computed: {
    name: function() {
      if (this.value) {
        return this.value.name;
      }
    },
    idEditorVisible: function() {
      return false;
    }
  },
  watch: {
    createMode(newValue) {
      if (newValue) {
        this.selectFindingTypeModalVisible = true;
      }
    }
  },
  methods: {
    handleFindingTypeModalCancel() {
      this.selectFindingTypeModalVisible = false;
    },

    mouseToolClick(toolId) {
      this.$emit("mouse-tool-click", tool.id);
      console.log(tool);
      switch (tool.type) {
        case "mouseTool":
          break;

        case "actionTool":
          this.$emit("action-tool-click", tool.id);
          break;

        default:
          break;
      }
    },

    findingSaveChanges() {
      var finding = { ...this.finding };
      this.$emit("finding-form-save-changes", finding);
    },

    findingDiscardChanges() {
      this.$emit("finding-form-discard-changes");
    }
  }
};
</script>


<style src="./DentalExamFindingEditor.scss" lang="scss" />

