import Paper from 'paper';

const MouseToolZoomOut = {

  id: 'zoomOut',

  mouseToolCursor: 'zoom-out.png',

  options: {},

  init() {
    let defaultOptions = {};
    this.config(defaultOptions);
  },

  config(options) {
    this.options = Object.assign(this.options, options);
  },

  build() {
    const mouseTool = new Paper.Tool();
    var newCenter;
    mouseTool.onMouseUp = function (event) {
      event.tool.view.zoom -= 0.25;
      if (event.tool.view.zoom == 1) {
        newCenter = new Paper.Point(325, 275);
        event.tool.view.center = newCenter;
      } else {
        newCenter = new Paper.Point();
        newCenter = newCenter.add(event.point);
        event.tool.view.center = newCenter;
      }
    };
    return mouseTool;
  }
};

export default MouseToolZoomOut;