const DentalExamMouseTools = {

  mouseTools: [
    // MouseToolPan,
    // MouseToolZoomIn,
    // MouseToolZoomOut,
    // MouseToolEraser,
    // MouseToolSelect,
    // TSXMT001,
    // TSXMT002,
    // TSXMT003,
    // TSXMT004,
    // TSXMT005,
    // TSXMT006,
    // TSXMT007,
    // TSXMT008,
    // TSXMT009
  ],

  init() {
    this.clearMouseTools();
  },

  addMouseTool(mouseTool) {
    this.mouseTools.push(mouseTool);
  },

  getMouseTool(mouseToolId, options) {
    console.log(this.mouseTools);
    var mouseTool = this.mouseTools.find(mouseTool => mouseTool.id === mouseToolId);
    if (mouseTool) {
      return mouseTool.build(options);
    }
    // eslint-disable-next-line
    console.log('Mouse Tool %s not found.', mouseToolId);
    return null;
  },

  clearMouseTools() {
    while (Paper.tool) {
      Paper.tool.remove();
    }
  },

  getMouseToolCursorPath(mouseToolId) {
    var mouseTool = this.mouseTools.find(mouseTool => mouseTool.id === mouseToolId);
    if (mouseTool) {
      return require(`@/assets/cursors/${mouseTool.mouseToolCursor}`);
    }
    // eslint-disable-next-line
    console.log('Mouse Tool %s not found.', mouseToolId);
    return null;
  }

};

