import Paper from 'paper';

const MouseToolPan = {

  id: 'pan',

  mouseToolCursor: 'move.png',

  options: {},

  init() {
    let defaultOptions = {};
    this.config(defaultOptions);
  },

  config(options) {
    this.options = Object.assign(this.options, options);
  },

  build() {
    const mouseTool = new Paper.Tool();
    mouseTool.onMouseDrag = function (event) {
      var point, downPoint;
      point = event.point;
      downPoint = event.downPoint;
      event.tool.view.center = new Paper.Point(
        event.tool.view.center.x + (downPoint.x - point.x),
        event.tool.view.center.y + (downPoint.y - point.y)
      );
    };
    return mouseTool;
  }

};

export default MouseToolPan;