<template>
  <a-modal
    :closable="false"
    :visible="visible"
    :footer="null"
    width="700"
    wrapClassName="dental-exam-finding-type-select-modal"
    centered
    @cancel="handleCancel"
  >
    <template slot="title">
      <span>BUSQUEDA DE HALLAZGO</span>
    </template>

    <a-input
      v-model="findingTypeSearchInput"
      placeholder="Escriba nombre de hallazgo"
      aria-label="Escriba nombre de hallazgo"
    >
      <v-icon name="search" slot="suffix"></v-icon>
    </a-input>

    <div class="finding-type-table-wrapper">
      <a-list
        :dataSource="findingTypesDataSource"
        :locale="{ emptyText: 'No se encontraron coincidencias' }"
        :rowKey="item => item.id"
        :split="false"
      >
        <a-list-item @click="handleSelect(item)" slot="renderItem" slot-scope="item">{{item.name}}</a-list-item>
      </a-list>
    </div>

    <!-- <autocomplete
      :search="findingTypeSearch"
      :get-result-value="getFindingTypeName"
      placeholder="Escriba nombre de hallazgo"
      aria-label="Escriba nombre de hallazgo"
      @submit="handleSubmit"
    />-->
  </a-modal>
</template>

<script>
const findingTypesTablesColumns = [
  {
    scopedSlots: { customRender: "name" }
  }
];

export default {
  name: "DentalExamFindingTypeSelectModal",

  props: {
    findingTypes: {
      type: Array,
      default: function() {
        return [];
      }
    },
    visible: {
      type: Boolean,
      default: function() {
        return false;
      }
    }
  },

  data() {
    return {
      findingTypesTableColumns: findingTypesTablesColumns,
      findingTypeSearchInput: ""
    };
  },
  computed: {
    findingTypesDataSource: function() {
      if (this.findingTypeSearchInput.length > 0) {
        let result = this.findingTypes.filter(findingType => {
          return findingType.name
            .toLowerCase()
            .startsWith(this.findingTypeSearchInput.toLowerCase());
        });
        return result;
      }
      return this.findingTypes;
    }
  },

  methods: {
    handleSelect(findingType) {
      if (findingType) {
        this.$emit("on-select", findingType);
      }
    },

    handleCancel() {
      console.log("asdasd");
      this.$emit("on-cancel");
    }
  }
};
</script>

<style src="./DentalExamFindingTypeSelectModal.scss" lang="scss" />
