import Paper from "paper";

const DentalExamOdontograph = {
  itemNameList: [
    "DP18",
    "DP17",
    "DP16",
    "DP15",
    "DP14",
    "DP13",
    "DP12",
    "DP11",
    "DP21",
    "DP22",
    "DP23",
    "DP24",
    "DP25",
    "DP26",
    "DP27",
    "DP28",
    "DP31",
    "DP32",
    "DP33",
    "DP34",
    "DP35",
    "DP36",
    "DP37",
    "DP38",
    "DP48",
    "DP47",
    "DP46",
    "DP45",
    "DP44",
    "DP43",
    "DP42",
    "DP41",
    "DP55",
    "DP54",
    "DP53",
    "DP52",
    "DP51",
    "DP61",
    "DP62",
    "DP63",
    "DP64",
    "DP65",
    "DP71",
    "DP72",
    "DP73",
    "DP74",
    "DP75",
    "DP85",
    "DP84",
    "DP83",
    "DP82",
    "DP81"
  ],

  ANNOTATIONS_LAYER: {

  },

  getDentaPieceNameList() {
    return this.itemNameList;
  },

  init() {
    Paper.setup("initial-odontograph");

    Paper.project.view.viewSize = new Paper.Size(650, 550);
    Paper.project.activeLayer.removeChildren();
    Paper.project.activeLayer.name = "BASE-LAYER";
    Paper.project.activeLayer.importSVG(
      require("../../assets/svg/base-layer.svg"), // eslint-disable-next-line
      item => {
        var items;
        items = this.getOdontographItems();

        items.forEach(dentalPieceItem => {
          var dentalPieceName = dentalPieceItem.name;
          var dentalPieceNumber = dentalPieceName.substr(2, 2);
          var dentalPieceGroups = dentalPieceItem.getItems();

          dentalPieceGroups.forEach(group => {
            if (group.name.startsWith("DPXX-TSX")) {
              group.name = `${dentalPieceName}-TSX`;
              group.getItems().forEach(tsxGroupItem => {
                if (tsxGroupItem.name.startsWith("DPXX-TSX-MT003")) {
                  tsxGroupItem.name = `${dentalPieceName}-TSX-MT003`;
                } else if (tsxGroupItem.name.startsWith("DPXX-TSX-MT004")) {
                  tsxGroupItem.name = `${dentalPieceName}-TSX-MT004`;
                } else if (tsxGroupItem.name.startsWith("DPXX-TSX-MT005")) {
                  tsxGroupItem.name = `${dentalPieceName}-TSX-MT005`;
                } else if (tsxGroupItem.name.startsWith("DPXX-TSX-MT006")) {
                  tsxGroupItem.name = `${dentalPieceName}-TSX-MT006`;
                } else if (tsxGroupItem.name.startsWith("DPXX-TSX-MT007")) {
                  tsxGroupItem.name = `${dentalPieceName}-TSX-MT007`;
                } else if (tsxGroupItem.name.startsWith("DPXX-TSX-MT008")) {
                  tsxGroupItem.name = `${dentalPieceName}-TSX-MT008`;
                }
              });
            } else if (group.name.startsWith("DPXX-T")) {
              group.name = `${dentalPieceName}-T`;
              group.getItems().forEach(textboxGroupItem => {
                if (textboxGroupItem.name.startsWith("DPXX-T-Textbox")) {
                  textboxGroupItem.name = `${dentalPieceName}-T-Textbox`;
                }
              });
            } else if (group.name.startsWith("DPXX-L")) {
              group.name = `${dentalPieceName}-L`;
              group.getItems().forEach(labelGroupItem => {
                if (labelGroupItem.name.startsWith("DPXX-L-Label")) {
                  labelGroupItem.name = `${dentalPieceName}-L-Label`;
                  labelGroupItem.content = `${dentalPieceNumber}`;
                  labelGroupItem.justify = "center";
                  labelGroupItem.bounds.center = group.position;
                }
              });
            } else if (group.name.startsWith("DPXX-S")) {
              group.name = `${dentalPieceName}-S`;
              group.getItems().forEach(shapeGroupItem => {
                if (shapeGroupItem.name.startsWith("DPXX-S-Shape")) {
                  shapeGroupItem.name = `${dentalPieceName}-S-Shape`;
                }
              });
            } else if (group.name.startsWith("DPXX-F")) {
              group.name = `${dentalPieceName}-F`;
              group.getItems().forEach(fillGroupItem => {
                if (fillGroupItem.name.startsWith("DPXX-F-R1")) {
                  fillGroupItem.name = `${dentalPieceName}-F-R1`;
                } else if (fillGroupItem.name.startsWith("DPXX-F-R2")) {
                  fillGroupItem.name = `${dentalPieceName}-F-R2`;
                } else if (fillGroupItem.name.startsWith("DPXX-F-R3")) {
                  fillGroupItem.name = `${dentalPieceName}-F-R3`;
                } else if (fillGroupItem.name.startsWith("DPXX-F-Upper")) {
                  fillGroupItem.name = `${dentalPieceName}-F-Upper`;
                } else if (fillGroupItem.name.startsWith("DPXX-F-Lower")) {
                  fillGroupItem.name = `${dentalPieceName}-F-Lower`;
                } else if (fillGroupItem.name.startsWith("DPXX-F-Left")) {
                  fillGroupItem.name = `${dentalPieceName}-F-Left`;
                } else if (fillGroupItem.name.startsWith("DPXX-F-Right")) {
                  fillGroupItem.name = `${dentalPieceName}-F-Right`;
                } else if (fillGroupItem.name.startsWith("DPXX-F-CUL")) {
                  fillGroupItem.name = `${dentalPieceName}-F-CUL`;
                } else if (fillGroupItem.name.startsWith("DPXX-F-CUM")) {
                  fillGroupItem.name = `${dentalPieceName}-F-CUM`;
                } else if (fillGroupItem.name.startsWith("DPXX-F-CUR")) {
                  fillGroupItem.name = `${dentalPieceName}-F-CUR`;
                } else if (fillGroupItem.name.startsWith("DPXX-F-CLL")) {
                  fillGroupItem.name = `${dentalPieceName}-F-CLL`;
                } else if (fillGroupItem.name.startsWith("DPXX-F-CLM")) {
                  fillGroupItem.name = `${dentalPieceName}-F-CLM`;
                } else if (fillGroupItem.name.startsWith("DPXX-F-CLR")) {
                  fillGroupItem.name = `${dentalPieceName}-F-CLR`;
                } else if (fillGroupItem.name.startsWith("DPXX-F-Center")) {
                  fillGroupItem.name = `${dentalPieceName}-F-Center`;
                }
              });
            }
          });
        });

        this.ANNOTATIONS_LAYER = new Paper.Layer();
        this.ANNOTATIONS_LAYER.name = "ANNOTATIONS_LAYER";

        Paper.project.view.update();
      }
    );

    // Paper.project.activeLayer.importSVG(require('../../assets/svg/t32.svg'));
    // Paper.project.activeLayer.importSVG(require('../../assets/svg/source2.svg'), (item) => {
    //   console.log(item);
    //   item.view.viewSize = new Paper.Size(607, 450);
    //   var search = Paper.project.getItems({
    //     class: Paper.PointText
    //   });
    //   search.forEach(text => {
    //     text.onMouseEnter = function(event) {
    //       this.fillColor = '#ffc247';
    //       this.strokeColor = '#ffc247';
    //       console.log(this);
    //       console.log('onMouseEnter');
    //     }
    //   });

    // });
  },

  getOdontographItems() {
    return this.itemNameList.map(itemName => {
      return Paper.project.getItems({
        name: itemName
      })[0];
    });
  },

  contains(point) {
    var it = this.getOdontographItems().values();
    var item = it.next();
    while (!item.done) {
      if (item.value.contains(point)) {
        return item.value;
      }
      item = it.next();
    }
    return false;
  },

  getOdontographBaseLayer() {
    var result = Paper.project.getItems({
      name: "BaseLayer"
    });

    return result[0];
  },

  createFindingLayer(findingId) {
    var findingLayer = new Paper.Layer();
    findingLayer.name = findingId;
    findingLayer.activate();
  },

  enableFocusMode(dentalPieceItem) {
    Paper.project.view.zoom = 4.5;
    var newCenter = new Paper.Point();
    newCenter = newCenter.add(dentalPieceItem.bounds.center);
    Paper.project.view.center = newCenter;
    var selected = dentalPieceItem.name;
    this.itemNameList.forEach(item => {
      var result = Paper.project.getItems({
        name: item
      });
      var odontographItem = result[0];
      if (odontographItem.name !== selected) {
        odontographItem.visible = false;
      }
    });
    Paper.project.activeLayer.getItems().forEach(path => {
      if (!path.isInside(dentalPieceItem.bounds)) {
        path.visible = false;
      }
    });
  },

  exitFocusMode() {
    Paper.project.view.zoom = 1;
    Paper.project.view.center = new Paper.Point(325, 275);
    this.itemNameList.forEach(item => {
      var result = Paper.project.getItems({
        name: item
      });
      var odontographItem = result[0];
      odontographItem.visible = true;
    });
    Paper.project.activeLayer.getItems().forEach(path => {
      path.visible = true;
    });
  },

  getActiveLayerData() {
    return Paper.project.activeLayer.exportJSON();
  }
};

export default DentalExamOdontograph;