import Paper from "paper";
import DentalExamOdontograph from "../DentalExamOdontograph/DentalExamOdontograph";

const MouseToolMT009 = {

  id: 'MT009',

  mouseToolCursor: 'move.png',

  options: {
    content: String,
    justification: String,
    fillColor: String,
    fontFamily: String,
    fontWeight: String,
    fontSize: Number
  },

  init() {
    let defaultOptions = {
      content: 'ABC',
      justification: 'center',
      fillColor: 'red',
      fontFamily: 'Courier New',
      fontWeight: 'bold',
      fontSize: 12
    };
    this.config(defaultOptions);
  },

  config(options) {
    this.options = Object.assign(this.options, options);
  },

  build() {
    const mouseTool = new Paper.Tool();
    var context = this;

    mouseTool.onMouseDown = function (event) {
      var item = DentalExamOdontograph.contains(event.point);

      if (item) {

        var findingBoxPath, newText;

        findingBoxPath = Paper.project.getItems({
          name: `${item.name}-T-Textbox`
        })[0];

        newText = new Paper.PointText({
          point: findingBoxPath.position,
          content: context.options.content,
          justification: context.options.justification,
          fillColor: context.options.fillColor,
          fontFamily: context.options.fontFamily,
          fontWeight: context.options.fontWeight,
          fontSize: context.options.fontSize
        });
        newText.bounds.center = findingBoxPath.position;
      }
    };

    return mouseTool;
  }

};

export default MouseToolMT009;