import Vue from "vue";
import nanoid from "nanoid";

import DentalExamActionTools from "@/core/DentalExamActionTools";
import DentalExamMouseTools from "@/core/DentalExamMouseTools/DentalExamMouseTools";
import DentalExamFindingTypes from "@/core/DentalExamFindingTypes/DentalExamFindingTypes";
import DentalExamOdontograph from "../../../core/DentalExamOdontograph/DentalExamOdontograph";

import {
  FINDING_EDITOR_MODE_CREATE,
  FINDING_EDITOR_MODE_EDIT
} from "@/core/FindingEditorMode";

import {
  ODONTOGRAPH_MODE_VIEW,
  ODONTOGRAPH_MODE_EDIT
} from "@/core/OdontographMode";

const initialState = {
  odontographMode: ODONTOGRAPH_MODE_VIEW,
  findingEditorMode: FINDING_EDITOR_MODE_CREATE,
  dentalPieceFocused: null,
  findingListVisible: true,
  findingEditorVisible: false,
  findingTypeSelectModalVisible: false,
  odontographCanvasId: "initial-odontograph",
  labelToolText: "",
  findingTypes: DentalExamFindingTypes.getFindingTypes(),
  activeMouseTool: null,
  finding: null,
  findingType: null,
  findingsData: []
};

export const state = {
  ...initialState
};

export const getters = {

  getFindingListData(state, getters, rootState) {
    var findings = rootState.dentalExam.initialOdontographData;
    return findings.map(finding => {
      return {
        id: finding.id,
        text: finding.name
      };
    });
  },

  getFindingTypeSelectModalVisible: state => {
    return state.getFindingTypeSelectModalVisible;
  },

  getFindingEditorVisible: state => {
    return state.findingEditorVisible;
  },

  getActiveMouseTool: state => {
    return state.activeMouseTool;
  },

  getActiveMouseToolColor: state => {
    return state.activeMouseToolColor;
  },

  getActiveMouseCursor: state => {
    return state.activeMouseCursor;
  },

  getCreateMode: state => {
    return state.createMode;
  },

  getEditMode: state => {
    return state.editMode;
  },

  getViewMode: state => {
    return state.viewMode;
  },

  getFindingListVisible: state => {
    return state.findingListVisible;
  },

  getFindingCreateVisible: state => {
    return state.findingCreateVisible;
  },

  getFindingFormVisible: state => {
    return state.findingFormVisible;
  },

  getActiveFindingType: state => {
    return state.activeFindingType;
  },

  getFindingTypes: state => {
    return state.findingTypes;
  },

  getActiveFinding: state => {
    return state.activeFinding;
  },

  getFocusMode: state => {
    return state.focusMode;
  },

  getOdontographItem: state => {
    return state.odontographItem;
  },

  getOdontographCanvasId: state => {
    return state.odontographCanvasId;
  },

  getLabelToolText: state => {
    return state.labelToolText;
  },

  getFindingsTableDataSource: state => {
    return state.findings.map(finding => {
      var rowData = {
        id: finding.id,
        text: DentalExamFindingTypes.getFindingTypeText(finding.typeId)
      };

      return rowData;
    });
  }
};

export const mutations = {

  setFindingType(state, findingType) {
    state.findingType = findingType;
  },

  addFinding(state, finding) {
    state.findings.push(finding);
  },

  editFinding(state, finding) {
    state.findings[state.findings.indexof(finding)] = finding;
  },

  deleteFinding(state, finding) {
    state.findings.splice(state.findings.indexof(finding), 1);
  },

  setFindings(state, findings) {
    state.findings = findings;
  },

  setFindingEditorVisible(state, findingEditorVisible) {
    state.findingEditorVisible = findingEditorVisible;
  },

  setCreateMode(state, createMode) {
    state.createMode = createMode;
  },

  setEditMode(state, editMode) {
    state.editMode = editMode;
  },

  setViewMode(state, viewMode) {
    state.viewMode = viewMode;
  },

  setActiveMouseTool(state, activeMouseTool) {
    state.activeMouseTool = activeMouseTool;
  },

  setActiveFindingLayer(state, activeFindingLayer) {
    state.activeFindingLayer = activeFindingLayer;
  },

  setActiveMouseToolColor(state, activeMouseToolColor) {
    state.activeMouseToolColor = activeMouseToolColor;
  },

  setActiveMouseCursor(state, activeMouseCursor) {
    state.activeMouseCursor = activeMouseCursor;
  },

  setFindingListVisible(state, findingListVisible) {
    state.findingListVisible = findingListVisible;
  },

  setFindingCreateVisible(state, findingCreateVisible) {
    state.findingCreateVisible = findingCreateVisible;
  },

  setFindingFormVisible(state, findingFormVisible) {
    state.findingFormVisible = findingFormVisible;
  },

  setActiveFindingType(state, findingType) {
    state.activeFindingType = findingType;
  },

  setActiveFinding(state, finding) {
    state.activeFinding = finding;
  },

  setFocusMode(state, focusMode) {
    state.focusMode = focusMode;
  },

  setOdontographItem(state, odontographItem) {
    state.odontographItem = odontographItem;
  },

  setOdontographCanvasId(state, odontographCanvasId) {
    state.odontographCanvasId = odontographCanvasId;
  },

  setLabelToolText(state, labelToolText) {
    state.labelToolText = labelToolText;
  },

  setFindingTypeSelectModalVisible(state, findingTypeSelectModalVisible) {
    state.findingTypeSelectModalVisible = findingTypeSelectModalVisible;
  },

  updateLayerData(state) {
    state.activeFinding.layerData = DentalExamOdontograph.getActiveLayerData();
  },

  resetState(state) {
    for (let f in state) {
      Vue.set(state, f, initialState[f]);
    }
  }

  // addFinding(state, data) {
  //   state.findingListData.push(data);
  // },

  // removeFinding(state, finding) {
  //   state.findingListData.splice(state.findingListData.indexOf(finding), 1);
  // },

  // editFinding(state, data) {

  //   state.findingListData = state.findingListData.map(findingData => {

  //     if (findingData.id !== data.id) {
  //       return findingData;
  //     }

  //     findingData.findingTypeName = data.findingTypeName;
  //     findingData.findingEditorTypeId = data.findingEditorTypeId;
  //     findingData.findingSubtypeId = data.findingSubtypeId;
  //     findingData.findingLayerData = data.findingLayerData;
  //     findingData.findingSpecifications = data.findingSpecifications;

  //     return findingData;

  //   });

  // },
};

export const actions = {
  addFinding({
    commit
  }, finding) {
    commit("dentalExam/addFinding", finding, {
      root: true
    });
  },
  handleFindingCreate({
    commit
  }, findingType) {
    var newFinding = {
      id: nanoid(10),
      typeId: findingType.id,
      layerData: null,
      specifications: "",
      remarks: ""
    };

    commit("setActiveFinding", newFinding);
    commit("setActiveFindingType", findingType);
    commit(
      "setActiveFindingLayer",
      DentalExamOdontograph.createLayer(newFinding.id)
    );
    commit("setViewMode", false);
    commit("setCreateMode", true);
    commit("setFindingListVisible", false);
    commit("setFindingEditorVisible", true);
  },
  handleFindingCreateCancel({
    state,
    commit
  }) {
    commit("setActiveFinding", null);
    commit("setActiveFindingType", null);
    commit("setActiveFindingType", null);
    DentalExamOdontograph.VIEW_LAYER.activate();
    commit("setCreateMode", true);
  },

  handleActionToolClick({
    getters,
    commit
  }, actionToolId = null) {
    if (actionToolId === null) {
      //eslint-disable-next-line
      console.log("actionToolId cannot be null.");
      return;
    }

    var actionTool = DentalExamActionTools.getActionTool(actionToolId);

    if (actionTool === null) {
      // eslint-disable-next-line
      console.log("Action Tool %s not found.", actionToolId);
      return;
    }
    actionTool.fireActionTool();
    DentalExamMouseTools.clearMouseTools();
    commit("setActiveMouseTool", null);
    commit("setActiveMouseCursor", "default");
  },

  handleMouseToolClick({
    state,
    getters,
    commit,
    dispatch
  }, mouseToolId) {
    if (state.activeMouseTool === mouseToolId) {
      DentalExamMouseTools.clearMouseTools();
      commit("setActiveMouseTool", null);
      commit("setActiveMouseCursor", "default");
      return;
    }

    DentalExamMouseTools.clearMouseTools();

    var mouseTool, mouseToolCursor;
    mouseTool = DentalExamMouseTools.getMouseTool(mouseToolId, {
      state,
      dispatch,
      getters
    });
    mouseTool.activate();
    mouseToolCursor = DentalExamMouseTools.getMouseToolCursorPath(mouseToolId);
    commit("setActiveMouseTool", mouseToolId);
    commit("setActiveMouseCursor", mouseToolCursor);
  },

  handleCreateFindingClick({
    commit
  }) {
    commit("setFindingTypeSelectVisible", true);
  },
  handleFindingCreateTypeSelected({
    commit
  }, findingType) {
    var findingItem = {
      id: nanoid(10),
      typeId: findingType.id,
      subtypeId: "",
      layerData: null,
      specifications: ""
    };

    DentalExamOdontograph.createFindingLayer(findingItem.id);

    commit("setActiveFinding", findingItem);
    commit("setActiveFindingType", findingType);
    commit("setFindingsVisible", false);
    commit("setFindingCreateVisible", false);
    commit("setFindingFormVisible", true);
    commit("setCreateMode", true);
  },
  handleFindingCreateCancelClick({
    commit
  }) {
    commit("setFindingCreateVisible", false);
    commit("setFindingFormVisible", false);
    commit("setFindingsVisible", true);
  },
  handleEditFindingClick({
    commit
  }, finding) {
    return;
  },
  handleRemoveFindingClick({
    commit
  }, finding) {
    return;
  },
  enableFocusMode({
    commit
  }, odontographItem) {
    // clear mouse tools
    DentalExamMouseTools.clearMouseTools();
    commit("setOdontographItem", odontographItem);
    commit("setFocusMode", true);
    commit("setActiveMouseTool", null);
    commit("setActiveMouseCursor", "default");
  },
  handleDisableFocusModeClick({
    commit
  }) {
    commit("setOdontographItem", {});
    commit("setFocusMode", false);
  },
  handleOdontographCanvasMounted({
    state,
    commit,
    dispatch
  }) {
    DentalExamOdontograph.init(state.odontographCanvasId, {
      commit,
      dispatch
    });
  },
  handleEditorSubtypeSelected({
    commit
  }, subtypeSelected) {
    commit("setLabelToolText", subtypeSelected);
  },

  handleChangeMouseToolColorClick({
    state,
    commit
  }) {
    switch (state.activeMouseToolColor) {
      case "color-red":
        commit("setActiveMouseToolColor", "color-blue");
        break;

      case "color-blue":
        commit("setActiveMouseToolColor", "color-red");
        break;

      default:
        commit("setActiveMouseToolColor", "color-red");
        break;
    }
  },

  handleEditorSaveClick({
    state,
    commit
  }) {
    if (state.createMode) {
      commit("updateLayerData");
      commit("addFinding", state.activeFinding);
      commit("setCreateMode", false);
    }

    if (state.editMode) {
      commit("updateLayerData");
      commit("editFinding", state.activeFinding);
      commit("setEditMode", false);
    }

    commit("setFindingFormVisible", false);
    commit("setFindingsVisible", true);
    commit("setViewMode", true);
  },

  handleEditorCancelClick({
    commit
  }) {},

  resetState({
    commit
  }) {
    commit("resetState");
  }
};

