import Paper from 'paper';

export const ActionToolExpand = {

  id: 'expand',

  options: {},

  onActionToolFired: {
    type: Function,
    default: function () {
      return;
    }
  },

  init() {
    var defaultOptions = {
      center: {
        x: 325,
        y: 275
      }
    };
    this.config(defaultOptions);
  },

  config(options) {
    this.options = Object.assign(this.options, options);
  },

  fire() {
    Paper.project.view.zoom = 1;
    Paper.project.view.center = new Paper.Point(
      this.options.center.x,
      this.options.center.y
    );
    this.onActionToolFired();
  }

};

const DentalExamActionTools = {

  actionTools: [],

  init() {
    this.actionTools = [];
  },

  addActionTool(actionTool) {
    this.actionTools.push(actionTool);
  },

  getActionTool(actionToolId) {

    var actionTool = this.actionTools.find(actionTool => actionTool.id === actionToolId);

    if (actionTool) {
      return actionTool;
    }

    return null;
  }
};

export default DentalExamActionTools;