<template>
  <div class="dental-exam-initial-odontograph">
    <div class="odontograph-wrapper">
      <a-card>
        <template v-if="!isFocusModeActive" slot="title">
          <h1 v-html="odontographCardTitle" />
          <dental-exam-navigation-toolbox
            :active-mouse-tool-name="activeMouseToolName"
            @on-mouse-tool-click="handleMouseToolClick"
            @on-action-tool-click="handleActionToolClick"
          />
        </template>

        <template v-if="isFocusModeActive" slot="title">
          <h1 v-html="odontographCardTitle" />
          <a-button shape="circle" @click="handleExitFocusMode()">
            <v-icon name="times" inverse></v-icon>
          </a-button>
        </template>

        <dental-exam-odontograph-canvas
          canvas-id="initial-odontograph"
          @on-mounted="handleOdontographCanvasMounted()"
        />

        <dental-exam-odontograph-toolbox
          :active-mouse-tool-name="activeMouseToolName"
          :active-mouse-tool-color="activeMouseToolColor"
          @on-mouse-tool-click="handleMouseToolClick"
          @on-change-mouse-tool-color-click="handleChangeMouseToolColorButtonClick"
        />
      </a-card>
    </div>

    <div v-if="findingListVisible" class="finding-list-wrapper">
      <a-card>
        <template slot="title">
          <h1>Registro de Hallazgos</h1>
          <a-button shape="circle" @click="handleNewFindingButtonClick()">
            <v-icon name="plus" scale="1.2" inverse></v-icon>
          </a-button>
        </template>

        <dental-exam-finding-list
          :data="getFindingListData"
          @on-create-finding="handleCreateFindingClick"
          @on-edit-finding="handleEditFindingClick"
          @on-delete-finding="handleDeleteFindingClick"
        />
      </a-card>
    </div>

    <div v-if="findingEditorVisible" class="finding-editor-wrapper">
      <a-card>
        <template slot="title">
          <a-button @click="handleFindingEditorBack">
            <v-icon name="arrow-left" inverse></v-icon>
          </a-button>

          <h1 v-html="findingEditorCardTitle" />

          <a-button @click="handleFindingEditorSubmit">
            <v-icon name="check" inverse></v-icon>
          </a-button>
        </template>
        <dental-exam-finding-editor
          v-model="finding"
          :mode="findingEditorMode"
          :finding-type="findingType"
        />
      </a-card>
    </div>

    <div class="specifications-wrapper">
      <a-card>
        <template slot="title">
          <h1>Especificaciones</h1>
        </template>

        <a-textarea
          placeholder="Escriba aqui las especificaciones del examen estomatologico"
          :rows="4"
        ></a-textarea>
      </a-card>
    </div>

    <div class="remarks-wrapper">
      <a-card>
        <template slot="title">
          <h1>Observaciones</h1>
        </template>

        <a-textarea
          placeholder="Escriba aqui las observaciones del examen estomatologico"
          :rows="4"
        ></a-textarea>
      </a-card>
    </div>

    <div v-if="findingTypeSelectModalVisible" class="finding-type-select-modal-wrapper">
      <dental-exam-finding-type-select-modal
        :visible="findingTypeSelectModalVisible"
        :finding-types="findingTypes"
        @on-select="handleFindingTypeSelectSelected"
        @on-cancel="handleFindingTypeSelectCancel"
      />
    </div>
  </div>
</template>

<script>
import Paper from "paper";
import nanoid from "nanoid";

import DentalExamOdontographCanvas from "@/components/DentalExamOdontographCanvas/DentalExamOdontographCanvas";
import DentalExamFindingList from "@/components/DentalExamFindingList/DentalExamFindingList";
import DentalExamFindingEditor from "@/components/DentalExamFindingEditor/DentalExamFindingEditor";
import DentalExamFindingTypeSelectModal from "@/components/DentalExamFindingTypeSelectModal/DentalExamFindingTypeSelectModal";
import DentalExamNavigationToolbox from "@/components/DentalExamNavigationToolbox/DentalExamNavigationToolbox";
import DentalExamOdontographToolbox from "@/components/DentalExamOdontographToolbox/DentalExamOdontographToolbox";

import { createNamespacedHelpers } from "vuex";

const {
  mapState,
  mapGetters,
  mapMutations,
  mapActions
} = createNamespacedHelpers("dentalExam/initialOdontograph");

import DentalExamOdontograph from "@/core/DentalExamOdontograph/DentalExamOdontograph";
import DentalExamMouseTools from "@/core/DentalExamMouseTools/DentalExamMouseTools";
import MouseToolSelect from "@/core/DentalExamMouseTools/MouseToolSelect";
import MouseToolPan from "@/core/DentalExamMouseTools/MouseToolPan";
import MouseToolZoomIn from "@/core/DentalExamMouseTools/MouseToolZoomIn";
import MouseToolZoomOut from "@/core/DentalExamMouseTools/MouseToolZoomOut";
import MouseToolEraser from "@/core/DentalExamMouseTools/MouseToolEraser";
import MouseToolMT001 from "@/core/DentalExamMouseTools/MouseToolMT001";
import MouseToolMT002 from "@/core/DentalExamMouseTools/MouseToolMT002";
import MouseToolMT003 from "@/core/DentalExamMouseTools/MouseToolMT003";
import MouseToolMT004 from "@/core/DentalExamMouseTools/MouseToolMT004";
import MouseToolMT005 from "@/core/DentalExamMouseTools/MouseToolMT005";
import MouseToolMT006 from "@/core/DentalExamMouseTools/MouseToolMT006";
import MouseToolMT007 from "@/core/DentalExamMouseTools/MouseToolMT007";
import MouseToolMT008 from "@/core/DentalExamMouseTools/MouseToolMT008";
import MouseToolMT009 from "@/core/DentalExamMouseTools/MouseToolMT009";

import DentalExamActionTools from "@/core/DentalExamActionTools";
import { ActionToolExpand } from "@/core/DentalExamActionTools";

export default {
  name: "DentalExamInitialOdontograph",
  components: {
    DentalExamOdontographCanvas,
    DentalExamFindingList,
    DentalExamFindingEditor,
    DentalExamFindingTypeSelectModal,
    DentalExamNavigationToolbox,
    DentalExamOdontographToolbox
  },

  props: {
    odontographData: []
  },

  data() {
    return {
      activeMouseTool: null,
      dentalPieceFocused: null,
      finding: null,
      activeMouseToolColor: "color-red"
    };
  },

  computed: {
    odontographCardTitle: function() {
      let cardTitle;
      if (!this.dentalPieceFocused) {
        cardTitle = "Odontograma";
      } else {
        let dentalPieceNumber = this.dentalPieceFocused.name.substr(-2);
        cardTitle = `Pieza Dental ${dentalPieceNumber}`;
      }
      return cardTitle;
    },
    findingEditorCardTitle: function() {
      return "Nuevo Hallazgo";
    },
    activeMouseToolName: function() {
      if (this.activeMouseTool) {
        return this.activeMouseTool.name;
      }
      return "empty";
    },
    isFocusModeActive: function() {
      return this.dentalPieceFocused ? true : false;
    },
    ...mapState([
      "findingsData",
      "findingListVisible",
      "findingEditorVisible",
      "findingTypeSelectModalVisible",
      "odontographMode",
      "findingEditorMode",
      "findingType",
      "findingTypes"
    ]),
    ...mapGetters([
      "getFindingTypes",
      "getFindingListData",
      "getOdontographData"
    ])
  },

  methods: {
    initOdontograph() {
      DentalExamOdontograph.init();
    },
    initMouseTools() {
      var context = this;
      MouseToolSelect.init();
      MouseToolSelect.config({
        onDentalPieceSelected: context.handleDentalPieceSelected
      });
      MouseToolPan.init();
      MouseToolZoomIn.init();
      MouseToolZoomOut.init();
      MouseToolEraser.init();
      MouseToolMT001.init();
      MouseToolMT002.init();
      MouseToolMT003.init();
      MouseToolMT004.init();
      MouseToolMT005.init();
      MouseToolMT006.init();
      MouseToolMT007.init();
      MouseToolMT008.init();
      MouseToolMT009.init();

      DentalExamMouseTools.init();
      DentalExamMouseTools.addMouseTool(MouseToolSelect);
      DentalExamMouseTools.addMouseTool(MouseToolPan);
      DentalExamMouseTools.addMouseTool(MouseToolZoomIn);
      DentalExamMouseTools.addMouseTool(MouseToolZoomOut);
      DentalExamMouseTools.addMouseTool(MouseToolEraser);
      DentalExamMouseTools.addMouseTool(MouseToolMT001);
      DentalExamMouseTools.addMouseTool(MouseToolMT002);
      DentalExamMouseTools.addMouseTool(MouseToolMT003);
      DentalExamMouseTools.addMouseTool(MouseToolMT004);
      DentalExamMouseTools.addMouseTool(MouseToolMT005);
      DentalExamMouseTools.addMouseTool(MouseToolMT006);
      DentalExamMouseTools.addMouseTool(MouseToolMT007);
      DentalExamMouseTools.addMouseTool(MouseToolMT008);
      DentalExamMouseTools.addMouseTool(MouseToolMT009);
    },
    initActionTools() {
      ActionToolExpand.init();
      ActionToolExpand.onActionToolFired = this.handleExpandToolFired;
      DentalExamActionTools.init();
      DentalExamActionTools.addActionTool(ActionToolExpand);
    },
    handleExpandToolFired() {
      DentalExamMouseTools.clearMouseTools();
      this.activeMouseTool = null;
    },
    handleNewFindingButtonClick() {
      this.setFindingTypeSelectModalVisible(true);
    },
    handleDentalPieceSelected(dentalPiece) {
      this.dentalPieceFocused = dentalPiece;
      this.activeMouseTool = null;
      DentalExamMouseTools.clearMouseTools();
    },
    handleActionToolClick(actionToolId) {
      let actionTool = DentalExamActionTools.getActionTool(actionToolId);
      if (actionTool) {
        actionTool.fire();
      }
    },
    handleMouseToolClick(mouseToolId) {
      if (!this.activeMouseTool || this.activeMouseTool.id !== mouseToolId) {
        DentalExamMouseTools.clearMouseTools();
        this.activeMouseTool = DentalExamMouseTools.getMouseTool(mouseToolId);
        this.activeMouseTool.name = mouseToolId;
        this.activeMouseTool.activate();
      } else {
        DentalExamMouseTools.clearMouseTools();
        this.activeMouseTool = null;
      }
    },
    handleOdontographCanvasMounted() {
      this.initOdontograph();
      this.initMouseTools();
      this.initActionTools();
    },
    handleExitFocusMode() {
      this.dentalPieceFocused = null;
      DentalExamOdontograph.exitFocusMode();
    },
    handleFindingTypeSelectSelected(findingType) {
      var newId = nanoid(7);
      var name = findingType.name;
      this.finding = {
        id: newId,
        name: name
      };

      this.activeMouseToolColor = "color-red";

      DentalExamMouseTools.mouseTools.forEach(tool => {
        if (tool.options.hasOwnProperty("content")) {
          tool.config({
            content: findingType.labelText
          });
        }
        if (tool.options.hasOwnProperty("strokeColor")) {
          tool.config({
            strokeColor: "red"
          });
        }
        if (tool.options.hasOwnProperty("fillColor")) {
          tool.config({
            fillColor: "red"
          });
        }
      });

      this.setFindingListVisible(false);
      this.setFindingTypeSelectModalVisible(false);
      this.setFindingEditorVisible(true);
    },
    handleFindingTypeSelectCancel() {
      this.setFindingTypeSelectModalVisible(false);
    },
    handleCreateFindingClick() {
      this.setFindingTypeSelectVisible(true);
    },
    handleEditFindingClick() {
      return;
    },
    handleDeleteFindingClick() {
      return;
    },
    handleFindingEditorSubmit() {
      var newFinding = { ...this.finding };
      this.addFinding(newFinding);

      this.finding = null;
      this.setFindingType(null);
      this.setFindingEditorVisible(false);
      this.setFindingListVisible(true);
    },
    handleFindingEditorBack() {
      this.setFindingEditorVisible(false);
      this.setFindingListVisible(true);
    },
    handleFindingEditorCancel() {
      return;
    },
    handleChangeMouseToolColorButtonClick() {
      var color;

      switch (this.activeMouseToolColor) {
        case "color-red":
          this.activeMouseToolColor = "color-blue";
          color = "blue";
          break;

        case "color-blue":
          this.activeMouseToolColor = "color-red";
          color = "red";
          break;

        default:
          this.activeMouseToolColor = "color-red";
          color = "red";
          break;
      }

      DentalExamMouseTools.mouseTools.forEach(tool => {
        if (tool.options.hasOwnProperty("strokeColor")) {
          tool.config({
            strokeColor: color
          });
        }

        if (tool.options.hasOwnProperty("fillColor")) {
          tool.config({
            fillColor: color
          });
        }
      });
    },
    isMouseToolActive(mouseToolName) {
      if (!this.activeMouseTool) return false;
      if (this.activeMouseTool.name === mouseToolName) return true;
      return true;
    },

    findingCustomToolActivate(customToolId) {},
    ...mapMutations([
      "setFindingEditorVisible",
      "setFindingTypeSelectModalVisible",
      "setFindingListVisible",
      "setFindingType"
    ]),
    ...mapActions(["addFinding"])
  }
};
</script>

<style src="./DentalExamInitialOdontograph.scss" lang="scss" />
