<template>
  <div class="dental-exam-finding-list">
    <a-table
      :rowKey="record => record.id"
      :dataSource="data"
      :columns="tableColumns"
      :locale="locale"
      :pagination="false"
      :showHeader="false"
      class="dental-exam-findings-table"
    >
      <template slot="findingText" slot-scope="record">
        <span class="finding-text">{{ record.text }}</span>
      </template>

      <!-- <template slot="findingAction" slot-scope="record">
        <div class="finding-actions">
          <a-button @click="editFinding(record)" shape="circle">
            <v-icon name="edit" scale="0.8"></v-icon>
          </a-button>

          <a-button @click="removeFinding(record)" shape="circle">
            <v-icon name="trash-alt" scale="0.8"></v-icon>
          </a-button>
        </div>
      </template>-->
    </a-table>
  </div>
</template>

<script>
export default {
  name: "DentalExamFindings",

  props: {
    data: {
      type: Array,
      default: []
    }
  },

  data() {
    return {
      tableColumns: [
        {
          key: "text",
          slots: { title: "findingText" },
          scopedSlots: { customRender: "findingText" }
        }
        // ,
        // {
        //   title: "",
        //   key: "action",
        //   scopedSlots: { customRender: "findingAction" }
        // }
      ],
      locale: {
        emptyText: "No se encontraron hallazgos registrados"
      }
    };
  },

  methods: {
    editFinding(finding) {
      this.$emit("on-edit-finding", finding);
    },

    removeFinding(finding) {
      this.$emit("on-remove-finding", finding);
    }
  }
};
</script>

<style src="./DentalExamFindingList.scss" lang="scss" />