import Vue from 'vue';

import patientInformation from '../dentalExam/modules/patientInformation';
import initialOdontograph from '../dentalExam/modules/initialOdontograph';
import evolutionOdontograph from '../dentalExam/modules/evolutionOdontograph';
import remarks from '../dentalExam/modules/remarks';



/* initial state */
const initialState = {
  id: null,
  findings: [],
  initialOdontographData: []
};

/* state */
export const state = {
  ...initialState
};

/* getters */
export const getters = {
  getInitialOdontographData(state) {
    console.log('aasdasd');
    return [];
  }
};


export const mutations = {
  addFinding(state, finding) {
    state.initialOdontographData.push(finding);
  },
  setDentalExam(state, dentalExam) {
    state.dentalExam = dentalExam;
  },

  resetState() {

  }

};

export const actions = {

  fetchDentalExam({
    commit
  }, dentalExamId) {
    commit('setDentalExam', null);
    return;
  },

  resetState({
    commit
  }) {
    commit('resetState');
  }

};

export default {
  namespaced: true,
  state: state,
  getters: getters,
  mutations: mutations,
  actions: actions,
  modules: {
    patientInformation: patientInformation,
    initialOdontograph: initialOdontograph,
    evolutionOdontograph: evolutionOdontograph,
    remarks: remarks
  }
};