<template>
  <div class="dental-exam-navigation-toolbox">
    <a-button
      shape="circle"
      :class="{ 'active-mouse-tool': activeMouseToolName === 'select'  }"
      @click="$emit('on-mouse-tool-click', 'select')"
      ghost
    >
      <v-icon name="mouse-tool-select" inverse></v-icon>
    </a-button>

    <a-button
      shape="circle"
      :class="{ 'active-mouse-tool': activeMouseToolName === 'pan'  }"
      @click="$emit('on-mouse-tool-click', 'pan')"
      ghost
    >
      <v-icon name="arrows-alt" inverse></v-icon>
    </a-button>

    <a-button
      shape="circle"
      :class="{ 'active-mouse-tool': activeMouseToolName === 'zoomIn'  }"
      @click="$emit('on-mouse-tool-click', 'zoomIn')"
      ghost
    >
      <v-icon name="search-plus" inverse></v-icon>
    </a-button>

    <a-button
      shape="circle"
      :class="{ 'active-mouse-tool': activeMouseToolName === 'zoomOut'  }"
      @click="$emit('on-mouse-tool-click', 'zoomOut')"
      ghost
    >
      <v-icon name="search-minus" inverse></v-icon>
    </a-button>

    <a-button shape="circle" @click="$emit('on-action-tool-click', 'expand')" ghost>
      <v-icon name="expand" inverse></v-icon>
    </a-button>
  </div>
</template>

<script>
export default {
  name: "DentalExamNavigationToolbox",
  props: { activeMouseToolName: "" }
};
</script>

<style src="./DentalExamNavigationToolbox.scss" lang="scss" />
  