<template>
  <div class="login-page">
      <a-card 
        class="card"         
        :bodyStyle="{ width: 470 + 'px' }"
      >
        <div class="logo">
          <img src="../../assets/img/coa-logo.png" alt="Centro Odontologico Americano">
        </div>
         <a-form
          id="components-form-demo-normal-login"
          :form="form"
          class="login-form"
          @submit="handleSubmit"
        >
          <a-form-item>
            <a-input
              v-decorator="[
                'userName',
                { rules: [{ required: true, message: 'El usuario es obligatorio' }] }
              ]"
              placeholder="Usuario"
              size="large"
            >
              <a-icon
                slot="prefix"
                type="user"
                style="color: rgba(0,0,0,.25)"
              />
            </a-input>
          </a-form-item>
          <a-form-item>
            <a-input
              v-decorator="[
                'password',
                { rules: [{ required: true, message: 'El password es obligatorio' }] }
              ]"
              type="password"
              placeholder="Contraseña"
              size="large"
            >
              <a-icon
                slot="prefix"
                type="lock"
                style="color: rgba(0,0,0,.25)"
              />
            </a-input>
          </a-form-item>
          <a-form-item>            
            <a-button
              html-type="submit"
              class="login-form-button"
              type="primary"
              size="large"
            >
              Iniciar Sesión
            </a-button>            
          </a-form-item>          
        </a-form>
        
        <a-alert v-show="loginIncorrect" message="Usuario y/o contraseña incorrectos." type="error" />

      </a-card>

  </div>
</template>

<script>

export default {
  name: 'LoginPage',
  data() {
    return {
      loginIncorrect: false,
      errorMessage: null,
    };
  },
  methods: {
    // login() {
    //   const username = this.$refs.username.value;
    //   const password = this.$refs.password.value;

    //   if (username.length !== 0 && password.length !== 0) {
    //     window.localStorage.setItem('authenticated', true);
    //     this.$router.push('/app/main/analytics');
    //   }
    // },
    handleSubmit (e) {
      e.preventDefault();
      this.form.validateFields((err, values) => {
        if (!err) {
          const user = this.form.getFieldValue('userName');
          const pass = this.form.getFieldValue('password');
          if(user === 'admin' && pass === 'coa2019') {
            window.localStorage.setItem('isAuthenticated', true);
            this.$router.push('/app/dentalexam');
          } else {
            this.form.resetFields();
            this.loginIncorrect = true;
          }          
        }
      });
    },
  },
  beforeCreate () {
    this.form = this.$form.createForm(this);
  },
  created() {
    if (window.localStorage.getItem('isAuthenticated') === 'true') {
      this.$router.push('/app/dentalexam');
    }
  },
};
</script>

