import Paper from "paper";
import DentalExamOdontograph from "../DentalExamOdontograph/DentalExamOdontograph";

const MouseToolMT002 = {

  id: 'MT002',

  mouseToolCursor: 'move.png',

  options: {
    fillColor: String,
    blendMode: String
  },

  init() {
    let defaultOptions = {
      fillColor: 'red',
      blendMode: 'multiply'
    };
    this.config(defaultOptions);
  },

  config(options) {
    this.options = Object.assign(this.options, options);
  },

  build() {
    const mouseTool = new Paper.Tool();
    var context = this;

    mouseTool.onMouseDown = function (event) {
      var item = DentalExamOdontograph.contains(event.point);

      if (item) {

        var typeA, typeB, typeC;

        typeA = ['DP18', 'DP17', 'DP16',
          'DP26', 'DP27', 'DP28',
          'DP36', 'DP37', 'DP38',
          'DP48', 'DP47', 'DP46',
          'DP55', 'DP54',
          'DP64', 'DP65',
          'DP85', 'DP84',
          'DP74', 'DP75',
        ];

        typeB = [
          'DP15', 'DP14',
          'DP24', 'DP25',
          'DP45', 'DP44',
          'DP34', 'DP35',
        ];

        typeC = ['DP13', 'DP12', 'DP11',
          'DP21', 'DP22', 'DP23',
          'DP31', 'DP32', 'DP33',
          'DP43', 'DP42', 'DP41',
          'DP53', 'DP52', 'DP51',
          'DP61', 'DP62', 'DP63',
          'DP71', 'DP72', 'DP73',
          'DP83', 'DP82', 'DP81',
        ];

        var dentalPieceName = item.name;
        var itemPathNameList = [];

        if (typeA.indexOf(dentalPieceName) >= 0) {
          itemPathNameList = [
            `${dentalPieceName}-F-CLR`,
            `${dentalPieceName}-F-CLM`,
            `${dentalPieceName}-F-CLL`,
            `${dentalPieceName}-F-CUR`,
            `${dentalPieceName}-F-CUM`,
            `${dentalPieceName}-F-CUL`,
            `${dentalPieceName}-F-Right`,
            `${dentalPieceName}-F-Left`,
            `${dentalPieceName}-F-Lower`,
            `${dentalPieceName}-F-Upper`
          ];
        } else if (typeB.indexOf(dentalPieceName) >= 0) {
          itemPathNameList = [
            `${dentalPieceName}-F-Center`,
            `${dentalPieceName}-F-Lower`,
            `${dentalPieceName}-F-Left`,
            `${dentalPieceName}-F-Right`,
            `${dentalPieceName}-F-Upper`
          ];
        } else if (typeC.indexOf(dentalPieceName) >= 0) {
          itemPathNameList = [
            `${dentalPieceName}-F-Lower`,
            `${dentalPieceName}-F-Left`,
            `${dentalPieceName}-F-Right`,
            `${dentalPieceName}-F-Upper`
          ];
        }

        var itemPathList = itemPathNameList.map(pathName => {
          return Paper.project.getItems({
            name: pathName
          })[0];
        });

        var it = itemPathList.values();
        var result = it.next();

        while (!result.done) {
          var regionPath = result.value;
          if (regionPath.contains(event.point)) {
            var path = regionPath.copyTo(Paper.project.activeLayer);
            path.bringToFront();
            path.fillColor = context.options.fillColor;
            path.blendMode = context.options.blendMode;
          }
          result = it.next();
        }
      }
    };

    return mouseTool;
  }

};

export default MouseToolMT002;