import Paper from 'paper';
import DentalExamOdontograph from '../DentalExamOdontograph/DentalExamOdontograph';

const MouseToolMT001 = {

  id: 'MT001',

  mouseToolCursor: 'move.png',

  options: {
    strokeColor: String,
    strokeWidth: Number,
    strokeCap: String,
    strokeJoin: String,
    blendMode: String
  },

  path: null,

  init() {
    let defaultOptions = {
      strokeColor: 'red',
      strokeWidth: '3',
      strokeCap: 'round',
      strokeJoin: 'round',
      blendMode: 'multiply'
    };
    this.config(defaultOptions);
  },

  config(options) {
    this.options = Object.assign(this.options, options);
  },

  build() {
    const mouseTool = new Paper.Tool();
    var context = this;

    mouseTool.onMouseDown = function (event) {
      if (DentalExamOdontograph.contains(event.point)) {
        context.path = new Paper.Path();
        context.path.strokeColor = context.options.strokeColor;
        context.path.strokeWidth = context.options.strokeWidth;
        context.path.strokeCap = context.options.strokeCap;
        context.path.strokeJoin = context.options.strokeJoin;
        context.path.blendMode = context.options.blendMode;
      }
    };

    mouseTool.onMouseDrag = function (event) {
      if (DentalExamOdontograph.contains(event.point)) {
        context.path.add(event.point);
      }
    };
    return mouseTool;
  }
};

export default MouseToolMT001;