import Paper from "paper";
import DentalExamOdontograph from "../DentalExamOdontograph/DentalExamOdontograph";

const MouseToolMT003 = {

  id: 'MT003',

  mouseToolCursor: 'move.png',

  options: {
    fillColor: String,
    blendMode: String
  },

  path: null,

  init() {
    let defaultOptions = {
      fillColor: 'red',
      blendMode: 'multiply'
    };
    this.config(defaultOptions);
  },

  config(options) {
    this.options = Object.assign(this.options, options);
  },

  build() {
    const mouseTool = new Paper.Tool();
    var context = this;

    mouseTool.onMouseDown = function (event) {
      var item = DentalExamOdontograph.contains(event.point);
      if (item) {
        var toolPath, newPath;
        toolPath = Paper.project.getItems({
          name: `${item.name}-TSX-MT003`
        })[0];
        newPath = toolPath.copyTo(Paper.project.activeLayer);
        newPath.bringToFront();
        newPath.fillColor = context.options.fillColor;
        newPath.blendMode = context.options.blendMode;
      }
    };

    return mouseTool;
  }

};

export default MouseToolMT003;