/* paper */
import Paper from 'paper';

/* dental exam odontograph items */
import DentalExamOdontograph from '@/core/DentalExamOdontograph/DentalExamOdontograph.js';

/* mouse tool select  */
const MouseToolSelect = {

  id: 'select',

  mouseToolCursor: 'mouse-pointer.png',

  options: {},

  init() {
    let defaultOptions = {
      onDentalPieceSelected: Function
    };
    this.config(defaultOptions);
  },

  config(options) {
    this.options = Object.assign(this.options, options);
  },

  build(options = {}) {
    options = Object.assign(this.options, options);
    const mouseTool = new Paper.Tool();
    const onDentalPieceSelected = options.onDentalPieceSelected;
    mouseTool.onMouseDown = function (event) {
      var dentalPiece = DentalExamOdontograph.contains(event.point);
      if (dentalPiece) {
        Paper.project.view.zoom = 4.5;
        var newCenter = new Paper.Point();
        newCenter = newCenter.add(dentalPiece.bounds.center);
        Paper.project.view.center = newCenter;
        var selected = dentalPiece.name;
        DentalExamOdontograph.getDentaPieceNameList().forEach(item => {
          var result = Paper.project.getItems({
            name: item
          });
          var odontographItem = result[0];
          if (odontographItem.name !== selected) {
            odontographItem.visible = false;
          }
        });
        Paper.project.activeLayer.getItems().forEach(path => {
          if (!path.isInside(dentalPiece.bounds)) {
            path.visible = false;
          }
        });
        onDentalPieceSelected(dentalPiece);
      }
    };
    return mouseTool;
  }
};

export default MouseToolSelect;